<template>
    <section>
        <div class="modal fade dialog my-popups ui-draggable show" style="height: auto; width: auto; display: block; top: 0px; left: 0px;" data-keyboard="false" data-backdrop="static" role="dialog">
            <div role="document" class="modal-dialog maxwidth500">
                <div class="modal-content">
                    <div class="modal-header">
                        <h5 id="myModalLabel" class="modal-title text-truncate pb-1">Upload a file </h5>
                        <span class="user-guide">
                            <a class="ancuserguide" data-toggle="collapse" href="javascript:;" role="button" aria-expanded="false"
                            aria-controls="collapseExample"> <em class="fa fa-files-o pr-2"></em>User Guide</a>
                            <div id="collapseReplyUG" class="divancuserguide collapse overflow-auto">
                            <div id="scrollbarreplyuserguideMangeView" class="custom-scrollbar-js">
                                <div class="con" v-html="$t('User_Guide_Dynamic_Manage_Folder')"></div>
                            </div>
                            </div>
                        </span>
                        <button type="button" data-dismiss="modal" class="close" v-on:click="closePopup"><i aria-hidden="true"
                                class="fa fa-times"></i></button>
                    </div>
                    <div class="modal-body">
                        <dynamic-form lang="en" ref="modalAddFile" :buttons="buttons" :schema="FormSchema" v-on:OnSubmit="onSubmit">
                            <template v-slot:tgslot-AttachmentFile="{ data }">
                                <!-- <a class="question-circle" href="javascript:;" data-toggle-tooltip="tooltip"
                                    :title="'Maximum upload size per file: ' + FileSizeAllowedToolTip + 'MB Allowed File Types: ' + FileTypeAllowedToolTip">
                                    <i class="fa fa-question-circle-o text-popover" style="font-size: 14px;"></i>
                                </a> -->
                                <div class="col-lg-12 pl-0">
                                    <div class="form-group">
                                        <div class="input-group">
                                            <div class="input-group">
                                                <div class="custom-file">
                                                    <input type="file" name="FileSrc1" id="flFile1" data-file="multi" @change="setFile($event, index)" tabindex="-1" style="position: absolute; clip: rect(0px, 0px, 0px, 0px);">
                                                    <div class="bootstrap-filestyle input-group">
                                                        <input type="text" disabled="disabled" class="form-control disabled" :value="getFileName">
                                                        <div class="input-group-append" style="cursor: pointer;">
                                                            <span tabindex="0" class="group-span-filestyle input-group-btn input-group-text bg-white choose-file">
                                                                <label title="Click here to select a file." for="flFile1" class="mb-0 font-weight-normal">Choose File</label>
                                                            </span>
                                                            <span class="input-group-text border-0 bg-transparent p-0 pl-2">
                                                                <a href="javascript:;" title="Clear" class="round-icon-small btn-danger-light">
                                                                    <i class="fa fa-undo"></i>
                                                                </a>
                                                            </span>
                                                        </div>
                                                    </div>
                                                </div>
                                            </div>
                                        </div>
                                    </div>
                                </div>                         
                            </template>

                            
                            <template v-slot:tgslot-CheckBoxMultiple="{ data1 }">
                                 <div class="col-md-12 col-lg-12 col-xl-12 float-left p-0 mb-2 lastchild-control" style="max-height: 450px; overflow-y: auto; border-bottom: 1px solid #dee2e6 !important; border-top: 1px solid #dee2e6 !important; ">
                                    <div  v-for="(userlist, indexuserlist)  in GetUserList" :key="indexuserlist">
                                        <div class="form-group row m-0 border p-2 bg-f5f5f5">
                                            <div class="custom-control custom-checkbox">
                                                <input type="checkbox" name="usertypes" :value="userlist.userTypeId"  v-on:change="CheckUserTypeAll" />
                                                <span class="clsprivdata">
                                                    {{ userlist.userType }}
                                                </span>
                                            </div>
                                        </div>
                                        <ul :id="userlist.userTypeId" class="row m-0 p-0 py-3 mb-3 border border-top-0">
                                            <li class="col-sm-12 col-md-4 col-lg-3 col-xl-3 float-left pl-2 mb-2" style="list-style:none;"   v-for="(inneruser, indexinneruser)  in userlist.userses" :key="indexinneruser">
                                                <div class="custom-control custom-checkbox">
                                                    <input type="checkbox" :value="inneruser.id" v-bind:checked="checkClass" /> 
                                                    {{ inneruser.name }}
                                                </div>
                                            </li>
                                        </ul>
                                    </div>
                                </div> 
                            </template>
                        </dynamic-form>
                        
                        <div class="py-2">
                            <span class="mandatory">Fields marked with an asterisk (*) are mandatory.</span>
                        </div>
                    </div>
                </div>
            </div>
        </div>    
    </section>
</template>

<script>
import DataService from '@/services/DataService';
export default{
    props:{
        ProjectDetails:{
            type:Array
        }
    },
    data(){
        return{
            GetUserList:[],
            getFileName:'',
            modelUserId:'',
            modelInnerUserId:'',
            checkClass:false,
            FormSchema: [
                {
                    layoutType: "double",
                    Data: [
                        {
                            astype: "TextField",
                            label: this.$t('FileTitle'),
                            name: "FileTitle",
                            value: "",
                            placeholder: "Enter File Title",
                            config: {
                                options: [],
                            },
                            validationRules: {
                                "required": true,
                            },
                        },
                        {
                            astype: "SlotField",
                            label: this.$t('File'),
                            name: "AttachmentFile",
                            value: "",
                            placeholder: "",
                            config: {
                                options: [],
                                showAddIcon: false,
                                onChange: this.onChangeDropdown
                            },
                            validationRules: "required",
                        },
                    ]
                },
                {
                    layoutType: "single",
                    Data:[
                        {
                            astype: "TextAreaField",
                            label: this.$t('Description'),
                            name: "Description",
                            value: "",
                            mode: "tag",
                            placeholder: "",
                            config: {
                                options: [],
                            },
                            validationRules: {
                                "required": true
                            },
                        }
                    ]
                },
                {
                    layoutType: "single",
                    group_name: 'Visible to',
                    group_display_name: this.$t('Visibleto'),
                    Data:[
                        {
                            astype: "SlotField",
                            label: "",
                            name: "CheckBoxMultiple",
                            value: "",
                            placeholder: "",
                            config: {
                                options: [],
                                showAddIcon: false,
                                onChange: this.onChangeDropdown
                            },
                            validationRules: "",
                        }
                    ]
                }
            ],
            buttons:[
                {
                    type: "submit",
                    class: "btn btn-success save",
                    text: "<i class='fa fa-upload pr-2'></i>Upload"
                },
                {
                    type: "button",
                    class: "btn btn-primary delete-btn mr-2 my-sm-0",
                    text: "<i class='fa fa-times pr-2'></i>Cancel",
                    onClick: this.CancelUpload
                }
            ]
        }
    },
    async created(){
        var vm = this;
        await vm.GetChecboxUserData();
    },
    methods:{
        closePopup(){
            this.$parent.ShowManageFile = false;
        },
        CancelUpload(){
            var vm = this;
            debugger
        },
        
        async GetChecboxUserData(){
            debugger;
            var vm = this;
            var params = `projectId=${vm.ProjectDetails.PROJECT_ID}`;
            await DataService.GetChecboxUserData(params).then(response=>{
                if(response.data.result.length > 0){
                    vm.GetUserList = response.data.result;
                }
            }).catch(err=>{
                console.log(err);
            })
        },
        setFile(event,b){
            debugger;
            var vm = this;
            vm.getFileName = event.target.files[0].name;
            vm.GetFiles = event.target.files;
        },
        onSubmit(){
            var vm = this;
            var getUserCheckedValue = vm.GetCheckedValue();
            var formdata = new FormData();
            var jsonbject = {
                'FileId':0,
                'FileName': null,
                'FilePath': null,
                'FileTitle': vm.FormSchema[0].Data[0].value,
                'FileContent':vm.FormSchema[1].Data[0].value,
                'UserList': getUserCheckedValue,
                'RequestType':"Post",
                'FileVersion':0,
                'ProjectId':0,
                'TaskId':0,
                'FileDetail':null,
                'StatusId':0,
                'FileCategoryId':null,
                'requestTypeValue':'first'
            }
            var postString = { "postString": jsonbject }
            formdata.append('Files',vm.GetFiles[0]);
            formdata.append('postString', JSON.stringify(postString));
            // var jsondata = JSON.stringify(jsonbject);
            // var params = `model=${JSON.stringify(jsonbject)}&${formdata}&`
            DataService.Create(formdata).then(res=>{
                debugger
            }).catch(err=>{
                console.log(err);
            })
        },
        CheckUserTypeAll(e){
            debugger;
            var vm  = this;
            
                if(e.target.checked){
                           
                        
                        vm.checkClass = true ;
                         
                   
                 
                    
                    
                }else{
                    vm.checkClass = false ;
                   
                }
                
        },
        
        GetCheckedValue(){
            var getvalue = []
            var getScreenshotids = document.getElementsByClassName('usertypes');
            for(var i=0; getScreenshotids[i]; ++i){
                if(getScreenshotids[i].checked){
                    getvalue.push(getScreenshotids[i].value);
                }
            }
            var getScreenshotids = document.getElementsByClassName('users');
            for(var i=0; getScreenshotids[i]; ++i){
                if(getScreenshotids[i].checked){
                    getvalue.push(getScreenshotids[i].value);
                }
            }
            var obj = getvalue.join(',');
            return obj;
        }
    }
}
</script>
<style>
  #AttachmentFile {
   position:relative;
 }
 #AttachmentFile .input-group .question-circle{
    position:absolute;
    top:-24px;
    left: 34px;
 } 
 .custom-file .bootstrap-filestyle input{   
 padding-bottom: 7px;
    margin-bottom: 3.55px;
}
.custom-file .bootstrap-filestyle .choose-file{
    margin-bottom:4px;
}
</style>